<template>
  <div class="app-index">
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
      <!-- header begin -->
      <van-sticky>
        <div class="xy-header xy-header-mall">
          <div class="xy-header-left">
            <div class="xy-header-tx">
              <van-image v-if="txurl" :src="txurl" />
            </div>
          </div>
          <div class="xy-header-search" @click="goSearch()">
            <!-- <router-link to="/search"> -->

            <!-- <div class="search-icon">
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#iconsousuo" />
              </svg>
            </div>
            <van-search
              ref="input0"
              name="search"
              :maxlength="50"
              is-amount
              shape="round"
              placeholder="请输入搜索条件"
              @search="onSearch"
            /> -->
            <search-box ref="searchBox" @query="onQueryChange" @goSearch="goSearch" />

            <!--
            </router-link> -->
          </div>
          <div class="xy-header-right" @click="goMallXxtxIndex()">
            <div class="right-function">
              <i class="iconfont iconxiaoxi" />
              <!-- <em v-if="num>0" class="num">{{ num }}</em> -->
            </div>
          </div>
        </div>
      </van-sticky>
      <!-- header end -->

      <!-- banner begin -->
      <div class="index-slide index-slide-mall lineGradient">
        <div class="index-slide-content">
          <!-- <van-image-preview v-model="show" :images="slide" @change="onChange">
            <template v-slot:index>第{{ indexs+1 }}页</template>
          </van-image-preview> -->
          <van-swipe
            ref="swiper"
            :is-prevent="false"
            :use-native-driver="false"
          >
            <van-swipe-item v-for="(item, $index) in slide" :key="$index">
              <div class="slide-item">
                <div class="slide-img"><img :src="item" @click="fd()"></div>
                <!-- <div class="slide-img" @click="imgPreview(item?item:defaultImg)"><img :src="item" @click="fd()"></div> -->
              </div>
            </van-swipe-item>
          </van-swipe>
        </div>
      </div>
      <!-- banner end -->

      <!-- 主体内容 begin -->
      <div class="index-main index-main-mall">
        <!-- <van-tabs id="boxFixed" class="xy-tabs" animated swipeable :class="{'is_fixed' : isFixed}"> -->
        <van-sticky class="tabs-sticky">
          <van-tabs class="xy-tabs" scrollspy sticky :offset-top="70">
            <div v-for="(tab,indexT) of navs" :key="indexT">
              <van-tab v-if="tab.dsbs===''" title="全部">
                <div class="shop-title"><h2>{{ tab.name }}</h2></div>
                <div class="goods-main">
                  <!-- 商品循环列表 begin -->
                  <div v-for="(item,index) in goodsList" :key="index" class="goods-list" @click="goGoodsdetail(item.name,item.id)">
                    <div class="goods-img">
                      <img :src="item.imagePath" :onerror="defaultImg">
                    </div>
                    <div class="goods-list-c">
                      <div class="goods-price">
                        <span class="num">{{ item.price_now }}</span>元
                        <del>
                          <span class="num">{{ item.price }}</span>元
                        </del>
                      </div>
                      <div class="goods-name">{{ item.name }}</div>
                      <!-- <div class="goods-description">冰翡翠 8G+128G 下单立送运动</div> -->
                      <div class="goods-sales"><em>已售{{ item.sale_count?item.sale_count:0 }}</em><em>{{ item.hp_count?item.hp_count:0 }}条好评</em></div>
                      <div v-if="item.sourceName" class="goods-label">
                        <van-tag plain type="primary">{{ item.sourceName }}</van-tag>
                      </div>
                    </div>
                  </div>
                  <!-- 商品循环列表 end -->
                </div>
                <div class="goods-list-more" @click="goMore('')">
                  查看更多商品
                  <i class="iconfont iconarrowright" />
                </div>
              </van-tab>
              <van-tab v-else-if="tab.dsbs==='JJZQ'" title="竞价专区">
                <div class="shop-title"><h2>竞价专区</h2></div>
                <div class="goods-main">
                  <!-- 商品循环列表 begin -->
                  <div v-for="(item,index) in JjzqProductList" :key="index" class="project-list">
                    <div class="project-list-c">
                      <div class="project-name">{{ item.xmmc }}</div>
                      <div class="project-countdown">
                        <span class="remaintime">{{ item.djs?item.djs.day:'' }}</span> 天 <span class="remaintime">{{ item.djs?item.djs.hour:'' }}</span> 时 <span class="remaintime">{{ item.djs?item.djs.minute:'' }}</span> 分
                      </div>
                      <div class="project-detail">
                        <ul>
                          <li>
                            <div class="fieldname"><em>时间</em>{{ item.fbsj?getTime(item.fbsj):'' }}</div>
                            <div class="fieldname"><em>单位</em>{{ item.yzdwmc }}</div>
                            <div class="fieldname"><em>联系人</em>{{ item.lxr }}</div>
                            <div class="fieldname"><span class="ybj"><van-icon name="volume-o" /></span><em>已报价</em>
                              <span class="num">{{ item.bjsl }}</span>条
                            </div>
                          </li>
                        </ul>
                      </div>
                      <div class="project-label">
                        <van-tag plain type="primary">{{ item.xmzt }}</van-tag>
                      </div>
                      <div class="list-btn">
                        <ul>
                          <li><van-button type="default" plain size="small" @click="wycy(item.xmxh)"><i class="iconfont iconzengjia" /> 我要参与</van-button></li>
                        </ul>
                      </div>
                    </div>
                  </div>
                  <!-- 商品循环列表 end -->
                </div>
                <div class="goods-list-more" @click="goMore('JJ')">
                  查看更多商品
                  <i class="iconfont iconarrowright" />
                </div>
              </van-tab>
              <van-tab v-else-if="tab.dsbs==='XJZQ'" title="询价专区">
                <div class="shop-title" @click="goMore('XJ')"><h2>询价专区</h2></div>
                <div class="goods-main">
                  <div v-for="(item,index) in XjzqProductList" :key="index" class="project-list">
                    <div class="project-list-c">
                      <div class="project-name">{{ item.xmmc }}</div>
                      <div class="project-countdown">
                        <span class="remaintime">{{ item.djs?item.djs.day:'' }}</span> 天 <span class="remaintime">{{ item.djs?item.djs.hour:'' }}</span> 时 <span class="remaintime">{{ item.djs?item.djs.minute:'' }}</span> 分
                      </div>
                      <div class="project-detail">
                        <ul>
                          <li>
                            <div class="fieldname"><em>时间</em>{{ item.fbsj?getTime(item.fbsj):'' }}</div>
                            <div class="fieldname"><em>单位</em>{{ item.yzdwmc }}</div>
                            <div class="fieldname"><em>联系人</em>{{ item.lxr }}</div>
                            <div class="fieldname"><span class="ybj"><van-icon name="volume-o" /></span><em>已报价</em>
                              <span class="num">{{ item.bjsl }}</span>条
                            </div>
                          </li>
                        </ul>
                      </div>
                      <div class="project-label">
                        <van-tag plain type="primary">{{ item.xmzt }}</van-tag>
                      </div>
                      <div class="list-btn">
                        <ul>
                          <li><van-button type="default" plain size="small" @click="wycy1(item.xmxh)"><i class="iconfont iconzengjia" /> 我要参与</van-button></li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="goods-list-more" @click="goMore('XJ')">
                  查看更多商品
                  <i class="iconfont iconarrowright" />
                </div>
              </van-tab>
              <van-tab v-else :title="tab.name">
                <div class="shop-title"><h2>{{ tab.name }}</h2></div>
                <div v-if="tab.list" class="goods-main">
                  <!-- 商品循环列表 begin -->
                  <div v-for="(item,index) in tab.list" :key="index" class="goods-list" @click="goGoodsdetail(item.name,item.id)">
                    <div class="goods-img">
                      <img :src="item.imagePath" :onerror="defaultImg">
                    </div>
                    <div class="goods-list-c">
                      <div class="goods-price">
                        <span class="num">{{ item.price_now }}</span>元
                        <del>
                          <span class="num">{{ item.price }}</span>元
                        </del>
                      </div>
                      <div class="goods-name">{{ item.name }}</div>
                      <div class="goods-sales"><em>已售{{ item.sale_count?item.sale_count:0 }}</em><em>{{ item.hp_count?item.hp_count:0 }}条好评</em></div>
                      <div v-if="item.sourceName" class="goods-label">
                        <van-tag plain type="primary">{{ item.sourceName }}</van-tag>
                      </div>
                    </div>
                  </div>
                  <!-- 商品循环列表 end -->
                </div>
                <div v-if="tab.list" class="goods-list-more" @click="goMore(tab.dsbs)">
                  查看更多商品
                  <i class="iconfont iconarrowright" />
                </div>
              </van-tab>
            </div>
          </van-tabs>
        </van-sticky>
      </div>
      <div class="common-space" />
    </van-pull-refresh>
    <footerNavMall :key="key" />
    <Backtop class="scrollTop-bar" />

    <!-- 返回首页 begin -->
    <div class="aside-fixed aside-fixed-bar">
      <div class="aside-icon" @click="goHome()">
        <i class="iconfont iconshouye" />
      </div>
    </div>
    <!-- 返回首页 end -->
  </div>
</template>

<script land="script" scoped>
import footerNavMall from '@/components/footerNav/footerNavMall.vue'
import { pageRouter } from '@/common/js/mixin.js'
import SearchBox from '@/components/search/search-box'
import { getSyHotProduct, getSyDszqhotProduct, category, getProImgList, dsjjzqList, dsxjzqList } from '@/store/module.js'
import Backtop from '@/components/backtop/backtop.vue' // 返回顶部
import { ImagePreview } from 'vant'
export default {
  name: 'MallHome',
  components: {
    footerNavMall,
    SearchBox,
    Backtop
  },
  mixins: [pageRouter],
  data() {
    return {
      key: 0,
      navs: [],
      indexs: '',
      show: false,
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"',
      // num: 0,
      XjzqProductList: [], // 询价专区商品列表
      JjzqProductList: [], // 竞价专区商品列表
      kjxyProductList: [], // 框架协议专区商品列表
      SnProductList: [], // 苏宁专区商品列表
      JdProductList: [], // 京东专区商品列表
      OfficemateProductList: [], // 欧菲斯专区商品列表
      isLoading: true,
      slide: [],
      goodsList: [], // 热门商品信息列表
      txurl: '' // 头像地址
    }
  },
  activated() {
    this.key++
  },
  created() {
    this.getSpMenu()
    this.$store.commit('mall/SET_ISACTIVE', 0)
    this.txurl = this.$store.state.mh.mhUserInfo.tx_url
    this.getImage()
    // this.getHotProduct()
    // this.getKjxyProduct('KJXY')
    // this.getJdProduct('JD')
    // this.getSnProduct('SN')
    // this.getOfficemateProduct('OFFICEMATE')
    // this.getXjzqProduct()
    // this.getJjzqProduct()
    // this.getXtggList()
  },
  methods: {
    getSpMenu() {
      category.data = { dsbs: this.dsbs }
      this.asyncApiModule(category).then(response => {
        const allNav = {
          id: 'ALL',
          dsbs: '',
          name: '全部',
          logo: 'e02c958ec7b7464c8aa2808cf57dac3a',
          zqtp: '99e3d39705ff40d2aa50cb5655cf47b9'
        }
        if (response.navs.length === 0) {
          this.navs = []
        } else {
          this.navs = response.navs
          this.navs.unshift(allNav)
          this.$store.commit('mall/SET_NAVS', this.navs)
        }
        for (let i = 0; i < this.navs.length; i++) {
          if (this.navs[i].dsbs === 'JJZQ') {
            this.getJjzqProduct()
          } else if (this.navs[i].dsbs === 'XJZQ') {
            this.getXjzqProduct()
          } else if (this.navs[i].dsbs === '') {
            this.getHotProduct()
          } else {
            this.getSnProduct(i)
          }
        }
      })
    },
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    },
    wycy1(xmxh) {
      this.$store.commit('bid/SET_XMXH', xmxh)
      this.$router.push({ name: 'XjDetail', params: { xmxh: xmxh }})
    },
    wycy(xmxh) {
      this.$store.commit('bid/SET_XMXH', xmxh)
      this.$router.push({ name: 'BidDetail', params: { xmxh: xmxh }})
    },
    fd() {
      this.show = true
    },
    onChange(index) {
      this.indexs = index
    },
    // getXtggList() {
    //   this.$store
    //     .dispatch('home/getNotices', { pageNo: '1', pageSize: '1000' })
    //     .then(res => {
    //       this.num = res.list.length
    //     })
    //     .catch(() => {
    //       this.loading = false
    //     })
    // },
    goMore(zq) {
      if (zq === 'JJ') {
        this.$router.push('BidList')
      } else if (zq === 'XJ') {
        this.$router.push('XjList')
      } else {
        this.$router.push({
          name: 'GoodsList',
          params: {
            zq: zq
          }
        })
      }
    },
    getTime(item) {
      item = item + ''
      return item.slice(0, 4) + '/' + item.slice(4, 6) + '/' + item.slice(6, 8) + ' ' + item.slice(8, 10) + ':' + item.slice(10, 12)
    },
    onQueryChange() {
    },
    goSearch(val) {
      this.$router.push({
        name: 'Search'
      })
    },
    // 获取询价专区商品列表
    getXjzqProduct() {
      dsxjzqList.data = {
        xmmc: '',
        pageno: 1,
        pagerow: 6
      }
      this.asyncApiModule(dsxjzqList).then(res => {
        this.XjzqProductList = res.list
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取竞价专区商品列表
    getJjzqProduct() {
      dsjjzqList.data = {
        pageno: 1,
        pagerow: 6
      }
      this.asyncApiModule(dsjjzqList).then(res => {
        this.JjzqProductList = res.list
      }).catch(() => {
        this.loading = false
      })
    },
    // 获取框架协议专区商品列表
    getKjxyProduct(zq) {
      getSyDszqhotProduct.data = { dsbs: zq }
      this.asyncApiModule(getSyDszqhotProduct).then(res => {
        this.kjxyProductList = res
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取京东专区商品列表
    getSnProduct(index) {
      getSyDszqhotProduct.data = { dsbs: this.navs[index].dsbs }
      this.asyncApiModule(getSyDszqhotProduct).then(res => {
        this.navs[index].list = res.productList
        console.log('this.navs---------------------------------------------------------', this.navs)
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取苏宁专区商品列表
    getJdProduct(zq) {
      getSyDszqhotProduct.data = { dsbs: zq }
      this.asyncApiModule(getSyDszqhotProduct).then(res => {
        this.JdProductList = res
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取欧菲斯专区商品列表
    getOfficemateProduct(zq) {
      getSyDszqhotProduct.data = { dsbs: zq }
      this.asyncApiModule(getSyDszqhotProduct).then(res => {
        this.OfficemateProductList = res
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取热门商品列表
    getHotProduct() {
      this.asyncApiModule(getSyHotProduct).then(res => {
        this.goodsList = res
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取轮播图
    getImage() {
      this.asyncApiModule(getProImgList).then(res => {
        this.slide = res
      })
        .catch(() => {
          this.loading = false
        })
    },
    onRefresh() {
      this.getImage()
      this.getSpMenu()
      this.key++
      this.isLoading = false
    },
    goGoodsdetail(name, id) {
      this.$store.commit('goods/SET_SPID', id)
      this.$store.commit('goods/SET_SPMC', name)
      this.$router.push({
        name: 'GoodsDetail',
        params: {
          refresh: true
        }
      })
    },
    goBidList() {
      this.$router.push({
        name: 'BidList'
      })
    },
    goMallXxtxIndex() {
      this.$router.push({
        name: 'MallXxtxIndex'
      })
    },
    goHome() {
      this.$router.push('MhHome')
    }
  }
}
</script>

<style lang="css" scoped>
.common-space{height: 150px;}
</style>
